import { Modal } from 'react-bootstrap';
import React from 'react';
import "./CommanModal.scss";


const CommanModal = (props) => {
    const { body, title, modalClass, ...rest } = props
    return (
        <Modal backdrop="static" {...rest} size="md" className={`themeModal ${modalClass}`} centered>
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                    {title}
                    </Modal.Title>
                </Modal.Header>
            <Modal.Body>
                {body}
            </Modal.Body>
        </Modal>
    )
}

export default CommanModal;