import React from "react";

export const Button = ({
    text,
    className,
    type,
    disabled,
    onClick,
    ref
}) => {

    return (
        <button
            type={type}
            className={`btn ${className}`}
            onClick={onClick}
            ref={ref}
            disabled={disabled}
            >
            {text}
        </button>
    );
};
